import { useRef, useState } from "react";
import { uploadFileToIPFS, uploadJSONToIPFS } from "./pinata";
import "../css/MintNFT.css";
// import logo from "../ .png";
// import { Link } from "react-router-dom";

// NFT  .
export default function MintNFT() {
  const [formParams, updateFormParams] = useState({
    name: "",
    description: "",
  });
  const [fileURL, setFileURL] = useState(null);
  const [message, setMessage] = useState("");
  const [imagePreview, setImagePreview] = useState(null);
  const nameInputRef = useRef(null);
  const descriptionInputRef = useRef(null);

  const { ethers } = require("ethers");

  //            .
  async function disableButton() {
    const listButton = document.getElementById("mint-button");
    listButton.disabled = true;
    listButton.style.backgroundColor = "grey";
    listButton.style.opacity = 0.3;
  }

  //         .
  async function enableButton() {
    const listButton = document.getElementById("mint-button");
    listButton.disabled = false;
    listButton.style.backgroundColor = "#9C8976";
    listButton.style.opacity = 1;
  }

  //   pinata IPFS    .
  //     ,       .
  async function OnChangeFile(e) {
    var file = e.target.files[0];

    try {
      disableButton();
      setMessage("Uploading image.. please dont click anything!");
      //  pinata .
      const response = await uploadFileToIPFS(file);
      if (response.success === true) {
        enableButton();
        setMessage("");
        console.log("Uploaded image to Pinata: ", response.pinataURL);
        //   ,  URL  fileURL .
        setFileURL(response.pinataURL);
        setImagePreview(URL.createObjectURL(file));
      }
    } catch (e) {
      console.log("Error during file upload", e);
    }
  }

  // nft (JSON) pinata   .
  // nft , ,     , nft    .
  // pinata           .
  async function uploadMetadataToIPFS() {
    const { name, description } = formParams;
    //   .
    if (!name) {
      setMessage("Please fill in the NFT name!");
      nameInputRef.current.focus();

      // 1.5  setMessage .
      setTimeout(() => setMessage(""), 1500);
      return -1;
    } else if (!description) {
      setMessage("Please fill in the NFT description!");
      descriptionInputRef.current.focus();

      // 1.5  setMessage .
      setTimeout(() => setMessage(""), 1500);
      return -1;
    }
    //  JSON      .
    const nftJSON = {
      name,
      description,
      image: fileURL,
    };

    try {
      // JSON   pinata .
      const response = await uploadJSONToIPFS(nftJSON);
      if (response.success === true) {
        console.log("Uploaded JSON to Pinata: ", response);
        // NFT  , NFT   URL   URL .
        return response.pinataURL;
      }
    } catch (e) {
      console.log("error uploading JSON metadata:", e);
    }
  }

  //   NFT  . OnClick .
  async function listNFT(e) {
    //       .
    e.preventDefault();

    try {
      // NFT  pinata .
      //    ,   URL  metadataURL .
      const metadataURL = await uploadMetadataToIPFS();
      console.log(metadataURL)
      if (metadataURL === -1) return;

      //     .
      const contractAddress = "0x295BC890632A6935691802A2F8d70E038c4F713f";
      const contractABI = require("../NFT.json");

      const provider = new ethers.BrowserProvider(window.ethereum);
      const signer = await provider.getSigner();
      disableButton();
      setMessage("Uploading NFT(takes 5 mins).. please dont click anything!");

      const contract = new ethers.Contract(
        contractAddress,
        contractABI,
        signer
      );

      // contract    .
      //  nft   URL .
      let transaction = await contract.mintNFT(metadataURL);
      await transaction.wait();

      alert("Successfully listed your NFT!");
      enableButton();
      setMessage("");
      updateFormParams({ name: "", description: "" });
      //   ,   .
      window.location.replace("/");
    } catch (e) {
      alert("Upload error" + e);
    }
  }

  return (
    <div>
      {/* <Link to="/profile"><div className='back'></div></Link>
        <Link to="/"><img src={logo} className='logo'/></Link>
        <Link to="/marketplace"><div className='back'></div></Link> */}
      <div className=" w-full lg:mt-[80px]  flex items-center justify-center ">
        <div
          id="nftForm"
          className="border border-mono60 shadow-lg rounded-md flex items-center p-10 justify-center lg:w-[40%] w-[90%] mt-[40px] lg:mt-0 lg:mb-[80px] mb-[40px]"
        >
          <form className="flex items-center flex-col w-full">
            <h3 className="text-center lg:text-[32px] text-brown100">
              Upload your NFT to the marketplace
            </h3>
            <div className="mt-[40px] w-full">
              <div>
                <div className="flex flex-col w-full">
                  <h3
                    className="text-brand100 text-[20px] mb-[9.5px]"
                    htmlFor="name"
                  >
                    NFT Name
                  </h3>
                  <input
                    className="border border-mono60 rounded-md p-2 w-full"
                    id="name"
                    type="text"
                    ref={nameInputRef}
                    placeholder="Write your NFT name!"
                    onChange={(e) =>
                      updateFormParams({ ...formParams, name: e.target.value })
                    }
                    value={formParams.name}
                  />
                </div>
              </div>
              <div>
                <div className="flex flex-col w-full">
                  <h3
                    className="text-brand100 text-[20px] mb-[9.5px] mt-[20px]"
                    htmlFor="description"
                  >
                    NFT Description
                  </h3>
                  <input
                    className="border border-mono60 rounded-md p-2 w-full h-[160px]"
                    id="description"
                    ref={descriptionInputRef}
                    type="text"
                    placeholder="Write the Description"
                    value={formParams.description}
                    onChange={(e) =>
                      updateFormParams({
                        ...formParams,
                        description: e.target.value,
                      })
                    }
                  />
                </div>
              </div>
              <div className="flex w-full mt-5 lg:flex-row flex-col">
                <h3 className="text-[20px]  text-brand100 " htmlFor="image">
                  Upload Image
                </h3>
                <input
                  type="file"
                  onChange={OnChangeFile}
                  id="imageFileInput"
                  style={{ display: "none" }}
                />
                {/*      file input   by.ddoqi */}
                <div
                  className="rounded-md p-1 font-semibold lg:ml-5 lg:mt-0 mt-3  hover:text-pink100 hover:bg-cream100 bg-brown100 hover:border hover:border-brown100 bg-opacity-70 text-cream100 shadow-lg text-center px-3 h-fit cursor-pointer text-[18px]"
                  onClick={() => {
                    const fileInput = document.getElementById("imageFileInput");
                    fileInput.click();
                  }}
                >
                  UPLOAD IMAGE
                </div>
              </div>
              <div className="border border-mono60 w-full h-[300px] mt-3 flex justify-center items-center text-mono60">
                {/*     */}
                {imagePreview ? (
                  <img
                    src={imagePreview}
                    alt="Uploaded Preview"
                    className="w-full h-full object-contain "
                  />
                ) : (
                  "IMAGE PREVIEW"
                )}
              </div>

              {message.length > 2 && (
                <div className="modal-container">
                  <div className="modal-content">{message}</div>
                </div>
              )}

              <div className="w-full flex items-center justify-center mt-[40px]">
                <button
                  onClick={listNFT}
                  id="mint-button"
                  className="shadow-xl bg-opacity-70 px-3 w-[55%] p-1 font-semibold rounded-md text-cream100 "
                >
                  LIST NFT !
                </button>
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
}
