import NFTTile from "./NFTTile";
import "../css/Marketplace.css";
import axios, * as others from 'axios';
import { useState } from "react";
import { GetIpfsUrlFromPinata } from "./utils";
import {Link } from "react-router-dom";
import logo from "../ .png";

export default function Marketplace() {
const [data, updateData] = useState([]);
const [dataFetched, updateFetched] = useState(false);

//  (nft  )  nft  .
async function getAllNFTs() {
    const nfts = [];
    const ethers = require("ethers");
  
    const contractAddress = "0x0e090AD02CF5DBF8c228Bb9cCb587E6C95635A53";
    const contractABI = require("../NFT.json");

    const provider = new ethers.BrowserProvider(window.ethereum);
    const signer =  await provider.getSigner();
  
    let contract = new ethers.Contract(contractAddress, contractABI, signer)
    
    //    smart contract    nft    .
    let transaction = await contract.getAllNFTs()

    //  nft   JSON   , items .
    const items = await Promise.all(transaction.map(async i => {
        if(i.tokenId != 0) {
            var tokenURI = await contract.tokenURI(i.tokenId);
          
                tokenURI = GetIpfsUrlFromPinata(tokenURI);
        
                let meta = await axios.get(tokenURI);
                meta = meta.data;

                //   JSON NFT  .
                let item = {
                    tokenId: Number(i.tokenId),
                    owner: i.owner,
                    image: meta.image,
                    name: meta.name,
                    description: meta.description,
                }
                return item;
                }
        else{
            return null;
        }
    }));
    // item tokenId 0      .
    // items    item nfts array .
    for (var i = 0; i < items.length; i++) {
        if(items[i] != null){
            nfts.push(items[i]);
        }
    }

    updateFetched(true);
    // nfts array updateData   data .
    updateData(nfts);
}


// getAllNFTs  .
if(!dataFetched)
    getAllNFTs();


//     . data state  nft  , data  
//    .
return ( 
    <>
    <Link to="/profile"><div className='back'></div></Link>
    <Link to="/mint"><div className='back'></div></Link>
    <Link to="/"><img src={logo} className='logo'/></Link>
    <div className="Container">
        <div className="Top-nft">
            NFT MarketPlace
        </div>
        {/*  nft NFTTile   */}
        <div className="total-nfts">
            {data.map((value, index) => {
                return <NFTTile data={value} key={index}></NFTTile>;
            })}
        </div>
    </div> 
    </>           
);

}